import React from 'react'
import { Fade } from "react-awesome-reveal";
function ThirdComponent(props) {

    
    
    return (
        <div>
            <Fade>
                <div>333</div>
                <div><button onClick={() => props.pageOnChange(1)}>2번페이지로 가기</button></div>
            </Fade>
        </div>
    )
}

export default ThirdComponent;
